<template>
  <div class="page-container">
    <base-breadcrumbs
      :parents="[['Home','/'], ['Klanten', '/klanten'], [currentClient.data().clientName, `/klant/${currentClient.id}`]]"
      :currentLocation="currentProject.data().projectName"
    />
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>
          <base-icon iconRef="archive" position="left"/>
          {{currentProject.data()
          .projectName}}
        </h2>
        <div class="flex-divider"></div>
        <base-button type="secondary" @click="openEditModal">
          <base-icon iconRef="edit" position="left"/>Bewerk
        </base-button>
      </div>
      <div class="page-box-content values">
        <div class="value-group" v-show="currentClient.data().description != ''">
          <div class="value">{{currentProject.data().description}}</div>
        </div>
        <div class="value-row">
          <div class="value-group">
            <div class="label">Adres</div>
            <div
              class="value"
            >{{`${currentProject.data().address.city}, ${currentProject.data().address.street} ${currentProject.data().address.houseNumber}, ${currentProject.data().address.postCode}`}}</div>
          </div>
        </div>
      </div>
      <div class="page-box-footer"></div>
    </div>
    <div class="page-box">
      <div class="page-box-title">
        <h3>
          <base-icon iconRef="cube" position="left"/>Ruimtes
        </h3>
        <div class="flex-divider"></div>
        <base-button @click="openCreateModal">
          <base-icon iconRef="plus" position="left"/>Nieuwe Ruimte
        </base-button>
      </div>
      <div class="page-box-datatable-container">
        <rooms-data-list :dataList="currentProjectRooms" @click="dataEntryClick($event)"/>
      </div>
    </div>
  </div>
</template>

<script>
import roomsDataList from "./data-list-components/rooms-data-list.vue";

export default {
  name: "Project",
  components: {
    roomsDataList
  },
  computed: {
    currentProject: function() {
      return this.$store.getters.getProjectsList.find(project => {
        return project.id == this.$route.params.projectId;
      });
    },
    currentClient: function() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.currentProject.data().belongsTo;
      });
    },
    currentProjectRooms: function() {
      return this.$store.getters.getRoomsList.filter(room => {
        return (
          room.data().belongsTo == this.currentProject.id &&
          room.data().trashed == false
        );
      });
    }
  },
  methods: {
    openEditModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "project",
        modalType: "edit",
        shown: true
      });
    },
    openCreateModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "room",
        modalType: "create",
        shown: true
      });
    },
    dataEntryClick: function(value) {
      this.openRoom(value);
    },
    openRoom: function(targetRoomId) {
      this.$router.push(`/ruimte/${targetRoomId}`);
    }
  },
  created: function() {
    this.$setPageTitle(`Project - ${this.currentProject.data().projectName}`);
  }
};
</script>

<style>
</style>



